/************** MealViewer **************/

#mealViewerConfirmBG {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 2;
  display: none;
  opacity: 0;
  touch-action: none;
  transition: opacity 350ms;
}
#mealViewerConfirmContainer {
  position: fixed;
  bottom: 0%;
  left: 50%;
  transform: translate(-50%, 50%);
  width: 80%;
  max-width: calc(var(--max-width) - 20%);
  padding: 20px;
  background-color: var(--background-white);
  border-radius: 5px;
  z-index: 2;
  text-align: center;
  box-shadow: 5px 5px 7px -6px rgba(0, 0, 0, 0.55);
  display: none;
  opacity: 0;
  transition: opacity 250ms, bottom 100ms;
}
#mealViewerConfirmContainer p {
  margin: auto;
  margin-bottom: 35px;
  color: var(--text-kinda-black);
  font-size: calc(17px + 6 * ((var(--vw) - 320px) / 680));
  text-align: center;
}
#mealViewerConfirmContainer button {
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  color: white;
  background-color: var(--main-color);
  padding: 3px 40px;
  border: none;
  border-radius: 5px;
  margin: 0 15px;
  cursor: pointer;
}
#mealViewerConfirmContainer button:nth-child(3) {
  background-color: red;
}
#mealViewerConfirmContainer button:hover {
  color: var(--text-kinda-black);
  font-weight: bold;
}
#mealViewerPage {
  width: 100%;
  max-width: var(--max-width);
  min-width: 320px;
  margin: auto;
  will-change: padding;
}
#mealViewerHeaderButtonsContainer {
  position: relative;
  width: 100%;
  height: 175px;
  background-color: var(--second-color);
}
#mealViewerHeaderItemName {
  position: absolute;
  bottom: 20px;
  right: 20px;
  color: var(--text-white);
  margin: 0;
  font-weight: bold;
  font-size: calc(25px + 6 * ((var(--vw) - 320px) / 680));
  width: 75%;
}
#mealViewerPageHeader {
  position: sticky;
  top: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr) 0.4fr;
  grid-template-rows: auto;
  align-items: center;
  direction: ltr;
  padding: 20px;
  background-color: var(--second-color);
  z-index: 1;
}
#mealViewerHeaderTitle {
  color: white;
  margin: 0;
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  text-align: center;
  grid-column-start: 3;
}
#mealViewerBackButton {
  fill: white;
  width: 35px;
  height: 35px;
  cursor: pointer;
}
#mealViewerBackButton:hover {
  fill: black;
}
#mealViewerEdit {
  fill: white;
  width: 35px;
  height: 35px;
  justify-self: end;
  cursor: pointer;
}
#mealViewerEdit:hover {
  fill: black;
}
#mealViewerTrashCan {
  fill: white;
  width: 35px;
  height: 35px;
  justify-self: end;
  cursor: pointer;
}
#mealViewerTrashCan:hover {
  fill: black;
}
#mealViewerHeaderTitle {
  color: white;
  margin: 0;
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  text-align: end;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
#mealViewerOkButton {
  fill: white;
  width: 40px;
  height: 40px;
  cursor: pointer;
}
#mealViewerOkButtonBg {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: -30px;
  left: 20px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: var(--main-color);
}
#mealViewerOkButtonAnimation1 {
  position: absolute;
  bottom: -30px;
  left: 20px;
  width: 60px;
  height: 60px;
  border: solid 1.5px var(--main-color);
  border-radius: 50%;
  box-sizing: border-box;
  pointer-events: none;
  animation: PulsAnimation 4s ease 1s infinite normal forwards;
}
#mealViewerOkButtonAnimation2 {
  position: absolute;
  bottom: -30px;
  left: 20px;
  width: 60px;
  height: 60px;
  border: solid 1.5px var(--main-color);
  border-radius: 50%;
  box-sizing: border-box;
  pointer-events: none;
  animation: PulsAnimation 4s ease 1.3s infinite normal forwards;
}
#mealViewerKcal {
  padding: 30px 40px 25px 0px;
  margin: 0;
  font-weight: bold;
  font-size: 40px;
  color: var(--text-kinda-black);
  background-color: var(--background-white);
}
#mealViewerKcal span {
  font-weight: normal;
  font-size: 20px;
  margin-right: 10px;
}
#mealViewerSelectMealContainer {
  background-color: var(--background-white);
  padding: 20px 0;
}
#mealViewerSelectMeal {
  display: block;
  width: 87%;
  height: 40px;
  margin: auto;
  color: var(--text-kinda-black);
  font-size: 18px;
  font-weight: bold;
  background-color: var(--background-white);
  border: none;
  border-radius: 0;
  border-bottom: solid 1px var(--text-kinda-black);
}
#mealNutritionInfoPercentContainer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 45px 0;
  background-color: var(--background-white);
}
#mealNutritionInfoPercentContainer div {
  width: 22%;
}
.mealPercentTitles {
  text-align: center;
  margin: 0;
  color: var(--text-kinda-black);
  font-size: calc(13px + 6 * ((var(--vw) - 320px) / 680));
}
.mealViewerInfoTitles {
  margin: 0;
  margin-top: 15px;
  padding: 20px;
  color: var(--text-kinda-black);
  font-weight: bold;
  font-size: 20px;
  background-color: var(--background-white);
}
@media (max-width: 450px) {
  #mealViewerBackButton {
    width: 30px;
    height: 30px;
  }
  #mealViewerEdit {
    width: 30px;
    height: 30px;
  }
  #mealViewerTrashCan {
    width: 30px;
    height: 30px;
  }
  #mealViewerOkButton {
    width: 30px;
    height: 30px;
  }
  #mealViewerOkButtonBg {
    width: 50px;
    height: 50px;
    bottom: -25px;
  }
  #mealViewerOkButtonAnimation1 {
    width: 50px;
    height: 50px;
    bottom: -25px;
  }
  #mealViewerOkButtonAnimation2 {
    width: 50px;
    height: 50px;
    bottom: -25px;
  }
}
