/************** NavicationBar **************/

#navicationBar {
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
  margin: 0;
  height: 80px;
  width: 100%;
  min-width: 320px;
  max-width: var(--max-width);
  text-align: center;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: var(--background-white);
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  z-index: 1;
  box-shadow: 0px -5px 10px -5px #00000040;
  touch-action: none;
}
.navElements {
  position: relative;
  display: inline-flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  cursor: pointer;
}
.navElements p {
  color: var(--main-color);
  margin: 0;
  font-size: 0px;
  transform-origin: bottom;
  opacity: 0;
  transition: all 0.2s;
}
.navElements svg {
  width: 20px;
  height: 20px;
  fill: var(--text-gray);
  transition: fill 0.2s;
}
@keyframes bubbleAnim {
  to {
    opacity: 0;
    transform: translate(-50%, -50%) scale(1.5);
  }
}
.navElements span {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: var(--main-color);
  opacity: 1;
  z-index: -100;
  animation: bubbleAnim 0.4s ease 0ms;
}

@media (max-width: 450px) {
  .navElements svg {
    width: 18px;
    height: 18px;
  }
  #navicationBar {
    height: 65px;
  }
  #addButton {
    bottom: 80px;
  }
}
@media (min-width: 740px) {
  #navicationBar {
    width: 80px;
    min-width: initial;
    height: 100vh;
    left: 0%;
    transform: initial;
    flex-direction: column;
    border-radius: 0px;
  }
}
